
"use client";
import React, { useState } from "react";
import { signInWithEmailAndPassword } from "firebase/auth";
import { auth, db } from "@/firebase/firebase";
import Navbar from '../../components/navbar'
import Footer from '../../components/footer'
import { getDoc, doc } from "firebase/firestore";
import { useRouter } from "next/navigation";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [userRole, setUserRole] = useState("");
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const handleLogin = async (e) => {
    e.preventDefault();
    setError("");
    setLoading(true);

    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;
      let userDoc = await getDoc(doc(db, "users", user.uid));
      let userData = userDoc.data();
      if (!userData) {
        userDoc = await getDoc(doc(db, "adminUsers", user.uid));
        userData = userDoc.data();
      }

      if (userData) {
        if (userData.status === 'locked' && userData.role !== 'superadmin' && userData.role !== 'admin' && userData.role !== 'moderator') {
          setError("T√†i kho·∫£n c·ªßa b·∫°n ƒë√£ b·ªã kh√≥a. Vui l√≤ng li√™n h·ªá qu·∫£n tr·ªã vi√™n.");
          return;
        }

        const role = userData.role || "user";
        setUserRole(role);
        if (role === "superadmin" || role === "admin" || role === "moderator") {
          console.log("ƒêƒÉng nh·∫≠p admin th√†nh c√¥ng, role:", role);
          router.push("/admin");
        } else if (role === "admin") {
          console.log("ƒêƒÉng nh·∫≠p admin th√†nh c√¥ng, role:", role);
          router.push("/admin");
        } else {
          console.log("ƒêƒÉng nh·∫≠p th√†nh c√¥ng, role:", role);
          router.push("/");
        }
      } else {
        console.log("Kh√¥ng t√¨m th·∫•y th√¥ng tin ng∆∞·ªùi d√πng trong Firestore");
        setError("T√†i kho·∫£n kh√¥ng t·ªìn t·∫°i trong h·ªá th·ªëng.");
      }
    } catch (err) {
      console.error("L·ªói ƒëƒÉng nh·∫≠p:", err.message);
      if (err.code === 'auth/user-not-found') {
        setError("Email kh√¥ng t·ªìn t·∫°i trong h·ªá th·ªëng.");
      } else if (err.code === 'auth/wrong-password') {
        setError("M·∫≠t kh·∫©u kh√¥ng ƒë√∫ng.");
      } else if (err.code === 'auth/too-many-requests') {
        setError("Qu√° nhi·ªÅu l·∫ßn ƒëƒÉng nh·∫≠p sai. Vui l√≤ng th·ª≠ l·∫°i sau.");
      } else {
        setError("Email ho·∫∑c m·∫≠t kh·∫©u kh√¥ng ƒë√∫ng.");
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <Navbar />
      <main>
        <div className="max-w-md mx-auto p-4 border rounded mt-8">
          <h1 className="text-2xl font-bold mb-4 text-center">ƒêƒÉng nh·∫≠p</h1>
          <form onSubmit={handleLogin} className="space-y-4">
            <input
              type="email"
              placeholder="Email"
              className="w-full border p-2"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              disabled={loading}
            />
            <input
              type="password"
              placeholder="M·∫≠t kh·∫©u"
              className="w-full border p-2"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              disabled={loading}
            />
            <button
              type="submit"
              className="bg-red-500 text-white py-2 px-4 rounded w-full hover:bg-red-600 disabled:opacity-50"
              disabled={loading}
            >
              {loading ? "ƒêang ƒëƒÉng nh·∫≠p..." : "ƒêƒÉng nh·∫≠p"}
            </button>
            <span>B·∫°n ch∆∞a c√≥ t√†i kho·∫£n? <a href="/dang-ky" className="text-blue-500">ƒêƒÉng k√Ω</a></span>
          </form>
          {error && <p className="text-red-500 mt-2">{error}</p>}
          {userRole && (
            <p className="text-green-600 mt-2">
              B·∫°n ƒëang ƒëƒÉng nh·∫≠p v·ªõi vai tr√≤: <strong>
                {userRole === 'superadmin' ? 'Super Admin' :
                  userRole === 'admin' ? 'Admin' :
                    userRole === 'moderator' ? 'Moderator' : userRole}
              </strong>
            </p>
          )}
        </div>
      </main>
      <Footer />
    </div>
  );
}
